import clsx from 'clsx';
import { FunctionalIcon } from '../icon/Icon';
import { DropDownItem, DropDownProps } from './DropDown.interface';
import { Listbox, Transition } from '@headlessui/react';
import React, { Fragment } from 'react';
import { getDistanceToBottom } from '../../../utils/getDistanceToBottom';
import { twMerge } from 'tailwind-merge';

const MENU_HEIGHT = 268;

const getOptionItemClasses = (hovered: boolean, isActive) => {
  return clsx(
    // TODO: replace zep-px-1 with `zep-px-dropdown-option-px` , zep-py-0.75 with `zep-py-dropdown-option-py`
    // TODO: add 'zep-bg-dropdown-option-bg-default' as default bg color
    'zep-flex zep-w-full zep-items-center zep-justify-between zep-px-1 zep-py-0.75',

    // TODO: 'zep-bg-dropdown-bg-readOnly' replace with 'zep-bg-dropdown-option-bg-hover'
    { 'zep-bg-dropdown-bg-readOnly/70': hovered }, // TailwindCSSThemer does not convert the alpha channel (@see https://github.com/RyanClementsHax/tailwindcss-themer/blob/main/docs/themingColors.md#opacity). Workaround: add tailwind opacity modifiers manually.

    // TODO: 'zep-bg-dropdown-bg-readOnly' replace with 'zep-bg-dropdown-option-bg-active'
    { 'zep-bg-dropdown-bg-readOnly': isActive },

    // TODO: replace 'zep-pr-3' with 'zep-py-dropdown-option-pr-active'
    { 'zep-pr-3': isActive },
  );
};

export const DropDown: React.FC<DropDownProps> = ({
  disabled = false,
  items,
  message,
  label,
  placeholder,
  readonly = false,
  state = 'default',
  ...props
}) => {
  const [selectedItem, setSelectedItem] = React.useState<DropDownItem>();
  const menuRef = React.useRef(null);
  const buttonRef = React.useRef(null);
  const hasError = state === 'error';
  const disabledOrReadonly = disabled || readonly;
  const iconColor = hasError
    ? 'zep-text-dropdown-support-error'
    : state === 'info'
    ? 'zep-text-dropdown-support-info'
    : 'zep-text-dropdown-support-default';

  return (
    <div
      className={twMerge(
        clsx('zep-relative', 'zep-inline-block', 'zep-text-left', {
          'zep-pointer-events-none zep-opacity-disabled': disabled, // TODO: replace `zep-opacity-disabled` with `zep-opacity-dropdown-disabled` && remove all other disabled styles from input, options, icon etc.
        }),
        props.className,
      )}
      data-testid="zep-drop-down"
    >
      <Listbox disabled={disabledOrReadonly} value={selectedItem} onChange={setSelectedItem}>
        {({ open }) => {
          const distanceToBottom = getDistanceToBottom(buttonRef.current);
          const openOnTop = distanceToBottom < MENU_HEIGHT;
          const displayValue = selectedItem?.name ?? placeholder;
          const color = disabled
            ? 'zep-text-dropdown-placeholder-disabled/40' // TailwindCSSThemer does not convert the alpha channel (@see https://github.com/RyanClementsHax/tailwindcss-themer/blob/main/docs/themingColors.md#opacity). Workaround: add tailwind opacity modifiers manually.
            : !selectedItem?.name && !readonly
            ? 'zep-text-dropdown-placeholder-readOnly'
            : 'zep-text-dropdown-placeholder-default';

          // input
          const buttonClasses = clsx(
            'zep-inline-flex',
            'zep-justify-between',
            'zep-rounded-dropdown',
            'zep-border-solid',
            'zep-pl-1', // TODO: -> zep-pl-dropdown-pl
            'zep-pr-0.75', // TODO: -> zep-pr-dropdown-pr
            'zep-py-0.75', // TODO: -> zep-py-dropdown-py
            'zep-font-roboto',
            'zep-w-[326px]', // TODO: would inherit the width from parent and set parent to width full unless you set a specific width on parent. Set this value as min-width instead
            'zep-outline-none',
            hasError ? 'zep-border-2' : 'zep-border-1', // TODO: should be zep-border-dropdown-width-error &  zep-border-dropdown-width-default
            color,
            { 'zep-bg-dropdown-bg-readOnly': readonly },
            { 'zep-ring': open },
            { 'zep-border-error': hasError },
            { 'zep-active:ring': !disabledOrReadonly },
            { 'zep-ring-offset-1': !disabledOrReadonly },
            { 'focus-visible:zep-ring': !disabledOrReadonly },
            { 'zep-ring-focus': !disabledOrReadonly },
            { 'zep-cursor-not-allowed': disabledOrReadonly },
          );

          const optionContainerClasses = clsx(
            'zep-drop-down-menu',
            'zep-absolute',
            'zep-py-0.25', // TODO: 'zep-py-dropdown-optionsContainer-py
            'zep-z-10',
            'zep-w-[326px]', // TODO: 'zep-min-w-dropdown'
            'zep-max-h-[268px]', // TODO: 'zep-max-h-dropdown-optionsContainer'
            'zep-origin-top-right',
            'zep-rounded-dropdown',
            'zep-bg-dropdown-bg-default', // TODO:  replace with zep-shadow-dropdown-optionsContainer-default
            'zep-drop-shadow-sm',
            'focus:outline-none',
            'zep-overflow-hidden',
            'zep-overflow-y-auto',
            openOnTop ? 'zep-mb-[-25px]' : 'zep-mt-0.25', // TODO: 'zep-mt-dropdown-optionsContainer'
            { 'zep-bottom-full': openOnTop },
          );

          const iconClasses = clsx(
            disabledOrReadonly ? 'zep-text-dropdown-placeholder-default/40' : 'zep-text-dropdown-placeholder-default',
            'zep-transition-transform',
            'zep-duration-500',
            'zep-shrink-0',
            {
              'zep-rotate-180': open,
            },
          );

          return (
            <>
              {label && (
                <Listbox.Label
                  className={clsx('zep-block', 'zep-mb-0.5', {
                    'zep-text-dropdown-label-disabled/40': disabled, // TODO: remove after using `zep-opacity-disabled` on parent
                  })}
                >
                  {label}
                </Listbox.Label>
              )}

              <Listbox.Button
                className={buttonClasses}
                ref={buttonRef}
                title={displayValue}
                data-testid="zep-drop-down-button"
              >
                <>
                  <span className="zep-truncate">{displayValue}</span>
                  <FunctionalIcon className={iconClasses} name="chevron-mini-down" role="img" size={24} />
                </>
              </Listbox.Button>
              <Transition
                as={Fragment}
                enter="zep-transition zep-ease-out zep-duration-100"
                enterFrom="zep-transform zep-opacity-0 zep-scale-95"
                enterTo="zep-transform zep-opacity-100 zep-scale-100"
                leave="zep-transition zep-ease-in zep-duration-25"
                leaveFrom="zep-transform zep-opacity-100 zep-scale-100"
                leaveTo="zep-transform zep-opacity-0 zep-scale-95"
              >
                <Listbox.Options className={optionContainerClasses} ref={menuRef} data-testid="zep-drop-down-menu">
                  {items.map((item, i) => (
                    <Listbox.Option
                      key={item.id}
                      value={item}
                      title={item.name}
                      className={({ active, selected }) => getOptionItemClasses(active, selected)}
                    >
                      {({ selected }) => {
                        return (
                          <>
                            <span className="zep-truncate">{item.name}</span>
                            {selected && (
                              <FunctionalIcon name="check" role="img" size={24} className="zep-absolute zep-right-1" />
                            )}
                          </>
                        );
                      }}
                    </Listbox.Option>
                  ))}
                </Listbox.Options>
              </Transition>
            </>
          );
        }}
      </Listbox>
      {message && (
        <div
          className={clsx(
            'zep-mt-[6px]', // TODO: 'zep-mt-dropdown-support-gap'
            'zep-tagline',
            'zep-text-0.875', // TODO: 'zep-text-dropdown-support'
            'zep-flex',
            'zep-items-center',
            iconColor,
          )}
        >
          {state !== 'default' && (
            <FunctionalIcon
              name="info-circle-filled"
              role="img"
              size={24}
              // TODO: replace with zep-gap-dropdown-support-gap on parent
              className="zep-mr-0.5"
            />
          )}
          <span>{message}</span>
        </div>
      )}
    </div>
  );
};
